import '../CssFiles/ContactListContainer.css';
import data from './Api'
import Chat from './Chat'
import React, { useState, useEffect} from "react";


function ContactList(props){ 
  const allChats = data.conversations;
  const [shadowval, setShadowVal] = useState();
  useEffect(() => {
    if (shadowval != null) {
      props.setChatID(shadowval);
    }
  }, [shadowval]);

  
  return(
    <div className="ContactConatiner">
{allChats.map((chat) => {
  if (chat.id === shadowval) {

  const val = 5;
  return (
      <Chat chat={chat} key={chat.id} setShadowVal={setShadowVal} val={val} />
      );
      }
      else{
        const val = 0;
          return (
            <Chat
              chat={chat}
              key={chat.id}
              setShadowVal={setShadowVal}
              val={val}
            />
          );
      }
      })}
    </div>
  );

}

export default ContactList;





