import React from 'react'
import '../CssFiles/Header.css';
import ArrowDropDownRoundedIcon from "@mui/icons-material/ArrowDropDownRounded";
import SendRoundedIcon from "@mui/icons-material/SendRounded";

const Header = () => {
  return (
    
    <div className="Header">
    <InputBox selectType="Query type"/>
        <InputBox selectType="Module" />
        <InputBox selectType="Sub Module" />
        <InputBox selectType="Chapter" />
    <hr className="HR"
    />
    <div className="lower-sec">
        <div className="cancel">CANCEL</div>
        <div className="apply-button">
          <SendRoundedIcon />

          <div className="apply-text">APPLY</div>
        </div>
      </div>
      </div>
    
  
  );
}
const InputBox=(props)=>{
    return(
      <div className="selector">
             <div className="selector-box">
               <span>Select</span>
               <div className="query-type">Select{" " + props.selectType}</div>
              <div className="options-icon">
                <ArrowDropDownRoundedIcon />
             </div>
            </div>
      
            <div
               className="box-message"
               style={{ marginLeft: "0.3rem", paddingTop: "4px" , fontSize:"10px" }}
             >
              Please select your{" " + props.selectType}
            </div>
            
          </div>
    );
}

export default Header
