import React,{useEffect, useState} from "react";
import { chatMessage } from "../JsFiles/Api";
import Message from "../JsFiles/Conversation";
import { v4 as uuidv4 } from "uuid";
import TextBox from "../JsFiles/TextBox";
import NewMassage from '../JsFiles/UserMassage';

function ChatScreen(props) {
  // const chatText = chatMessage.chats;
  // console.log(chatText);
  const [chatText, setchatText] = useState([]);
  const [sentText, setSentText] = useState([{ id: null, textMessage: "" }]);
  
  useEffect(() => {
    setchatText(
      chatMessage.chats.filter((chat) => chat.conversationId === props.chatID)
    );
  }, [props.chatID]);


  const sendMessage = (input) => {
    setSentText([...sentText, { id: props.chatID, textMessage: input }]);
  };
  
  return (
    <div className="chat-screen">
      {props.chatID != null ? (
        <div className="all-chats">
          {chatText.map((chatline) => {
            return <Message chatline={chatline} key={uuidv4()} />;
          })}
          {sentText
            .filter((currChats) => currChats.id === props.chatID)
            .map((text) => {
              return <NewMassage text={text.textMessage} key={uuidv4()} />;
            })}
        </div>
      ) : (
        ""
      )}
      <TextBox sendMessage={sendMessage}/>
    </div>
  );
}

export default ChatScreen;
